import * as React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import CardActionArea from "@mui/material/CardActionArea";
import axios from "axios";
import { useState, useEffect } from "react";

export default function ActionAreaCard() {
  const [coinname, setCoinName] = useState("");
  const [value, setValue] = useState("");
  const [price, setPrice] = useState("");
  const [twentyfourhourChange, settwentyfourhourChange] = useState("");

  async function getStats() {
    try {
      const BitcoinrResult = await axios.get(
        `${process.env.backend_url}/coins/stats?coin=bitcoin`
      );
      console.log(BitcoinrResult.data);
      setCoinName("Bitcoin");
      setValue(BitcoinrResult.data.current_price);
      setPrice(BitcoinrResult.data.market_cap);
      settwentyfourhourChange(BitcoinrResult.data.price_change_percentage_24h);
    } catch (err) {
      console.error(err);
    }
  }

  useEffect(() => {
    getStats();
    const interval = setInterval(() => {
      getStats();
    }, 1000);

    return () => clearInterval(interval);
  }, []);
  return (
    <Card sx={{ maxWidth: 345, boxShadow: 5 }}>
      <CardActionArea>
        <CardMedia
          component="img"
          height="140"
          image="/static/assets/bitcoin_img.jpg"
          alt="Bitcoin Image"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            {coinname || "Loading..."}
          </Typography>
          <Typography variant="body2" sx={{ color: "text.secondary" }}>
            Value: $ {value || "Loading..."} <br />
            Price: $ {price || "Loading..."} <br />
            24-Hour Change: $ {twentyfourhourChange || "Loading..."}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
