// Card5.js (Polkadot)
import * as React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import CardActionArea from "@mui/material/CardActionArea";
import axios from "axios";
import { useState, useEffect } from "react";

export default function Card5() {
  const [coinname, setCoinName] = useState("Polkadot");
  const [value, setValue] = useState("");
  const [price, setPrice] = useState("");
  const [twentyfourhourChange, settwentyfourhourChange] = useState("");

  async function getStats() {
    try {
      const polkadotResult = await axios.get(
        `${process.env.backend_url}/coins/stats?coin=polkadot`
      );
      setValue(polkadotResult.data.current_price);
      setPrice(polkadotResult.data.market_cap);
      settwentyfourhourChange(polkadotResult.data.price_change_percentage_24h);
    } catch (err) {
      console.error(err);
    }
  }

  useEffect(() => {
    getStats();
  }, []);

  return (
    <Card sx={{ maxWidth: 345, boxShadow: 5 }}>
      <CardActionArea>
        <CardMedia
          component="img"
          height="140"
          image="/static/assets/polkadot.jpg"
          alt="Polkadot Image"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            {coinname || "Loading..."}
          </Typography>
          <Typography variant="body2" sx={{ color: "text.secondary" }}>
            Value:$ {value || "Loading..."} <br />
            Price:$ {price || "Loading..."} <br />
            24-Hour Change:$ {twentyfourhourChange || "Loading..."}{" "}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
